import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Table } from 'react-bootstrap';
//import Form from 'react-bootstrap/Form';
import Header from "../Header";
//import  Footer  from "../Footer";
import { Link } from 'react-router-dom'
//import { useHistory } from 'react-router-dom';
//import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
//import BookingDetails from './BookingDetails';
import ClipLoader from "react-spinners/ClipLoader";
import { withRouter } from 'react-router-dom';
import Pagination from "@material-ui/lab/Pagination";
class History extends Component {

    state = { userId: '', item: [], error: '', isLoading: false, loadingColor: '#ffffff', pageId: 1, rowCount: 0, totalPage: 0 };
    constructor(props) {
        super(props);
        //console.log("++pickup**********==="+JSON.stringify(this.props));    
    }

    componentDidMount() {
        this.setState({ isLoading: true });
        let userId = localStorage.getItem("userId");
        //let token=localStorage.getItem("token");
        this.setState({ userId: userId });

        if (userId == "" || userId == null) {
            window.location.href = "/Home";
        }
        //this.setState({item:this.props.match.params.data});
        this.getHistory(userId, 1);
    }

    async getHistory(userId, pageId) {

        let token = localStorage.getItem("token");
        const headers = { 'Authorization': `Bearer ${token}` };
        // let headers["Authorization"] = `Bearer ${token}`;
        let urlData = "&userId=" + userId + "&pageId=" + pageId;
        //const response = await fetch('http://localhost:3001/booking/getCabs?originObj='+originObj+'&destinationObj='+destinationObj, { headers });
        //console.log("urlData=="+urlData+"==headers="+JSON.stringify(headers));
        const response = await fetch(process.env.REACT_APP_APIURL + 'user/get_user_booking?' + urlData, { headers });

        const data = await response.json();

        if (data.code == 200) {
            this.setState({ totalPage: data.totalPage });
            this.setState({ rowCount: data.rowCount });
            this.setState({ item: data.data });
        } else {
            this.setState({ error: 'some internal error please try later' })
        }
        this.setState({ isLoading: false });
        //this.setState({cabsList:data.data});
    }
    handlePageChange = async (event, value) => {
        //this.setState({pageId:value});
        let userId = this.state.userId;
        let pageId = value;
        this.setState({ pageId: pageId });
        this.getHistory(userId, pageId);
    }
    render() {
        const override = `
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (

            <div>
                <Header />
                <div>
                    <ClipLoader css={override} style={{ borderColor: 'red' }} color={this.state.loadingColor} loading={this.state.isLoading}
                        size={150} >  </ClipLoader>
                    <section id="pricing" className="pricing">

                        <div className="container" data-aos="fade-up" style={{ width: '95%!important' }}>

                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, padding: 10, color: 'white',}}>Booking History </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                <Table striped bordered responsive hover>
                                                    <thead>
                                                        <tr>
                                                            <th>BookingId</th>
                                                            <th>Pickup</th>
                                                            <th>Destination</th>
                                                            <th>PickupDate</th>
                                                            <th>ReturnDate</th>
                                                            <th>Amount</th>
                                                            <th>Paid</th>
                                                            <th>Status</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            this.state.item.map(function (object, i) {
                                                                return <tr key={i+1}> <td>
                                                                    <Link variant="success" style={{ padding: "4px", fontSize: 12, textAlign: "center" }} to={{ pathname: `/BookingDetails/${object.orderId}` }} >{object.orderId}</Link></td>
                                                                    <td>{object.pickup}</td>
                                                                    <td>{object.destination}</td>
                                                                    <td>
                                                                        {object.pickupDate != null ? new Intl.DateTimeFormat('en-GB', {
                                                                            month: 'long',
                                                                            day: '2-digit',
                                                                            year: 'numeric',
                                                                            hour: 'numeric',
                                                                            minute: 'numeric',
                                                                            hour12: true
                                                                        }).format(new Date(object.pickupDate)) : null
                                                                        }
                                                                    </td>
                                                                    <td>
                                                                        {object.returnDate != null ? new Intl.DateTimeFormat('en-GB', {
                                                                            month: 'long',
                                                                            day: '2-digit',
                                                                            year: 'numeric',
                                                                            hour: 'numeric',
                                                                            minute: 'numeric',
                                                                            hour12: true
                                                                        }).format(new Date(object.returnDate)) : null
                                                                        }
                                                                    </td>
                                                                    <td>{object.finalAmount} </td>
                                                                    <td>{object.paid} </td>
                                                                    <td>{object.status} </td>
                                                                </tr>;
                                                            })
                                                        }

                                                    </tbody>
                                                </Table>
                                            </div>
                                            <div>
                                                <Pagination
                                                    className="paging"
                                                    count={this.state.totalPage}
                                                    page={this.state.pageId}
                                                    defaultPage={this.state.pageId}
                                                    siblingCount={2}
                                                    boundaryCount={2}
                                                    color="primary"
                                                    showFirstButton showLastButton
                                                    shape="rounded"
                                                    onChange={this.handlePageChange.bind()}
                                                />
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>

                        </div>

                    </section>


                </div>
            </div>
        );
    }
}

export default withRouter(History);