import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button, Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Header from "../Header";
import Footer from "../Footer";
import { Link } from 'react-router-dom'
import { useHistory } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';
import Pagination from "@material-ui/lab/Pagination";
class Cars extends Component {

    state = { userId: '', item: [], error: '', isLoading: false, loadingColor: '#ffffff', pageId: 1, payment_orderId: '', carModelName: '', carType: '', carNo: '', rcBook: '' };
    constructor(props) {
        super(props);
        //console.log("++pickup**********==="+JSON.stringify(this.props));    
    }

    componentDidMount() {
        this.setState({ isLoading: true });
        let userId = localStorage.getItem("userId");
        this.setState({ userId: userId });

        //this.setState({item:this.props.match.params.data});
        this.getCars(userId, 1);
    }

    showPopup(object) {

        this.prePayment(object);
    }
    async getCars(userId, pageId) {

        //const headers = { 'Content-Type': 'application/json' } 
        let token = localStorage.getItem("token");
        const headers = { 'Authorization': `Bearer ${token}` };
        let urlData = "&userId=" + userId + "&pageId=" + pageId;
        const response = await fetch(process.env.REACT_APP_APIURL + 'admin/get_cars?' + urlData, { headers });
        //console.log("+++response=="+JSON.stringify(response))
        const dataRes = await response.json();
        if (dataRes.code == 200) {
            this.setState({ totalPage: dataRes.totalPage });
            this.setState({ rowCount: dataRes.rowCount });
            this.setState({ item: dataRes.data });
        } else {
            console.log("errorr")
            //this.setState({error:'No data found'})
        }
        this.setState({ isLoading: false });
        //this.setState({cabsList:data.data});
    }
    
    invalidCar = async(obj) => {
        try {
            console.log("obj:"+obj.id);
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            //let urlData = "&userId=" + this.state.userId + "&driverId=" + obj.id + "&status=block";
            const urlData = { userId: this.state.userId,carId:obj.id,status:'invalid'};
            const result = await axios.put(process.env.REACT_APP_APIURL + "admin/update_car_status", urlData, { headers });
            console.log(result.data + "resultpay:" + JSON.stringify(result.data));
            if (result.data.code == 200) {
                this.setState({ error: 'Car blocked' });
                this.getCars(this.state.userId, this.state.pageId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
    }
    deleteCar = async(obj) => {
        try {
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            const urlData = { userId: this.state.userId,carId:obj.id,status:'delete'};
            const result = await axios.put(process.env.REACT_APP_APIURL + "admin/update_car_status", urlData, { headers });
            console.log(result.data + "resultpay:" + JSON.stringify(result.data));
            if (result.data.code == 200) {
                this.setState({ error: 'Car Deleted' });
                this.getCars(this.state.userId, this.state.pageId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
    }
    approveCar = async (obj) => {
        try {
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&carId=" + obj.id + "&status=approved";
            const result = await axios.put(process.env.REACT_APP_APIURL + "admin/update_car_status", urlData, { headers });
            console.log(result.data + "resultpay:" + JSON.stringify(result.data));
            if (result.data.code == 200) {
                this.setState({ error: 'Car approved' });
                this.getCars(this.state.userId, this.state.pageId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
    }
    handlePageChange = async (event, value) => {
        //this.setState({pageId:value});
        let userId = this.state.userId;
        let pageId = value;
        this.setState({ pageId: pageId });
        this.getCars(userId, pageId);
    }
    render() {
        const override = `
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (

            <div>
                <Header />
                <div>
                    <ClipLoader css={override} style={{ borderColor: 'red' }} color={this.state.loadingColor} loading={this.state.isLoading} size={150} >  </ClipLoader>
                </div>


                <div>
                    <section id="pricing" className="pricing">

                        <div className="container" data-aos="fade-up" style={{ width: '95%!important' }}>

                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>All Cars </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                <Table striped bordered hover responsive>
                                                    <thead>
                                                        <tr>
                                                            <th>Car model</th>
                                                            <th>Number </th>
                                                            <th>Type</th>
                                                            <th>Rc</th>
                                                            <th>Vendore</th>
                                                            <th>Status</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            this.state.item.map((object, i) => {
                                                                return <tr key={i+1}>
                                                                    <td>{object.carModelName}</td>
                                                                    <td>{object.carNo}</td>
                                                                    <td>{object.carType}</td>
                                                                    <td><a href={object.rcBook} target="_blank"> <img src={object.rcBook} width={100} /></a></td>
                                                                    <td>{object.User.firstName} {object.User.lastName}</td>
                                                                    <td>
                                                                        
                                                                    {object.status == 'pending' ? <div>
                                                                            <Button title='Delete' onClick={this.deleteCar.bind(this,object)} variant="danger" size='small' style={{ padding: "10px", fontSize: 12, textAlign: "center", margin: 2 }}>
                                                                                <i className="fa fa-remove iconStyle" aria-hidden="true"></i>
                                                                            </Button>
                                                                            <Button title='Approve' onClick={this.approveCar.bind(this,object)} variant="success" size='small' style={{ padding: "10px", fontSize: 12, textAlign: "center", margin: 2 }}>
                                                                                <i className="fa fa-check iconStyle" aria-hidden="true"></i>
                                                                            </Button>
                                                                        </div> : <div>
                                                                            <Button title='Delete' onClick={this.deleteCar.bind(this,object)} variant="danger" size='small' style={{ padding: "10px", fontSize: 12, textAlign: "center", margin: 2 }}>
                                                                                <i className="fa fa-remove iconStyle" aria-hidden="true"></i>
                                                                            </Button>
                                                                            <Button title='Invalid' onClick={this.invalidCar.bind(this,object)} variant="secondary" size='small' style={{ padding: "10px", fontSize: 12, textAlign: "center", margin: 2 }}>
                                                                                <i className="fa fa-ban iconStyle" aria-hidden="true"></i>
                                                                            </Button>
                                                                            
                                                                        </div>
                                                                        }
                                                                        {object.User.status}
                                                                    </td>


                                                                </tr>;
                                                            })
                                                        }

                                                    </tbody>
                                                </Table>
                                            </div>
                                            <div>
                                                <Pagination
                                                    className="paging"
                                                    count={this.state.totalPage}
                                                    page={this.state.pageId}
                                                    defaultPage={this.state.pageId}
                                                    siblingCount={2}
                                                    boundaryCount={2}
                                                    color="primary"
                                                    showFirstButton showLastButton
                                                    shape="rounded"
                                                    onChange={this.handlePageChange.bind()}
                                                />
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>

                        </div>

                    </section>


                </div>
            </div>
        );
    }
}

export default withRouter(Cars);